@import "../Src/css/lux.scss";

// hover animation for skills list
html {
  scroll-behavior: smooth;
}
.profile {
  border: 4px solid;
  @extend .border-warning;
  width: 200px;
  height: 200px;
  overflow: hidden;
}
#skills {
  ul {
    strong {
      transition: all 0.2s ease;
    }
    &:hover strong {
      @extend .text-warning;
    }
  }
}
#portfolio {
  button:hover,
  button.active {
    @extend .btn-warning;
  }

  .card {
    transition: all 0.8s ease;
    .card-img-top {
      height: 220px;
      background-size: cover;
      background-position: top center;
      transition: all 0.8s ease-out;
    }
    &:hover .card-img-top {
      background-position: bottom center;
    }
    &:hover {
      @extend .shadow;
    }
  }
}
nav {
  border-bottom: 4px solid;
  @extend .border-warning;
  @extend .shadow-sm;
}
footer {
  border-top: 4px solid;
  @extend .border-warning;
}
